import axios from 'axios'

// Ê†πÊçÆÂΩìÂâçÂüüÂêçËÆæÁΩÆ baseURL
let baseURL = ''

const hostname = window.location.origin

if (hostname === "https://game.ccfly.cc" || hostname === "http://localhost:3000") {
	baseURL = "https://slot55xx3.ccfly.cc"
} else {
	baseURL = hostname.replace('//game.', '//')
}

// ÂàõÂª∫ axios ÂÆû‰æã
const service = axios.create({
	baseURL,
	timeout: 10000, // ËØ∑Ê±ÇË∂ÖÊó∂Êó∂Èó¥ 
})

// ËØ∑Ê±ÇÊã¶Êà™Âô®
service.interceptors.request.use(
	config => {
		const token = localStorage.getItem('token')
		if (token) {
			config.headers.Authorization = `Bearer ${token}`
		}

		// üëá Ê∑ªÂä†ÈªòËÆ§ Content-Type
		config.headers['Content-Type'] = 'application/json'

		return config
	},
	error => Promise.reject(error)
)

// ÂìçÂ∫îÊã¶Êà™Âô®
service.interceptors.response.use(
	response => {
		// Ê†πÊçÆÂêéÁ´ØÁªìÊûÑÂ§ÑÁêÜÔºàÂÅáËÆæ code=200 Ë°®Á§∫ÊàêÂäüÔºâ
		const res = response.data
		if (res.meta.code !== 0) {
			// ÂèØÊ†πÊçÆÂêéÁ´ØËøîÂõûÁöÑ code ÂÅöÁªü‰∏ÄÂ§ÑÁêÜ
			console.error(res.message || 'ËØ∑Ê±ÇÂá∫Èîô')
			return Promise.reject(res)
		}
		return res
	},
	error => {
		// Áªü‰∏ÄÈîôËØØÊèêÁ§∫
		if (error.response) {
			if (error.response.status === 401) {
				// alert('Êú™ÊéàÊùÉÊàñÁôªÂΩïËøáÊúü')
				// location.href = '/login' Á≠âË∑≥ËΩ¨
			}
		}
		return Promise.reject(error)
	}
)

export default service